.footer {
    position: relative;
    z-index: 50;
    padding-top: 86px;
    font-size: 22px;
    overflow: hidden;
    @include inner();
    
    &__inner {
        padding-top: 27px;
        padding-bottom: 50px;
        border-top: 1px solid $border;
    }
    
    @media only screen and (max-width: 1024px) {
        padding-top: 50px;
        font-size: 20px;
    }
}